import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { MyReferralsPage } from './my-referrals.page';

const routes: Routes = [
  {
    path: '',
    component: MyReferralsPage
  },
  {
    path: 'redeem-history',
    loadChildren: () => import('./redeem-history/redeem-history.module').then( m => m.RedeemHistoryPageModule)
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class MyReferralsPageRoutingModule {}
